<template>
  <div class="box1">
    <div class="title">
      <h2>对于吃货来说，一切幸福都是从吃开始的。对于幸福，一切都是从放假开始的。</h2>
    </div>
    <textarea rows="5" cols="30" v-model="obj.comment" placeholder="欢迎添加美食点评"></textarea>
    <br>
   <div class="add_discuss"> <button @click="btnSubmit" class="btn1">提交评论</button>
    <router-link tag="button" to="/menu">返回</router-link></div>
  </div>
</template>
<script>
  import {request} from "../../../network/request";
  export default {
    name: "MenuComment",
    data(){
      return{
        obj:{
          comment:'',
          menuId:this.$route.query.id
        }
      }
    },
    methods:{
      btnSubmit(){
        if(localStorage.getItem('token')){
          const jon = JSON.stringify(this.obj);
          request({
            url: '/menuComment',
            method: 'post',
            data: jon,
            headers: {'Content-Type': 'application/json;charset=UTF-8'}
          }).then(res => {
            console.log(res.data.message);
            alert("评论成功");
            this.$router.push({path:'/menucontent',query:{id:this.obj.menuId}})
          }).catch(err => {
            console.log(err);
          });
        }
        else{
          this.$router.push('/login');
        }

      }
    }
  }
</script>

<style scoped>
  .box1{
    /* position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%); */
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  .btn1{
    margin-left: 108px;
    margin-right: 10px;
  }
  button{
    border:2px solid #F6652C;
    background-color:#F6652C;
    height:30px;
    width:70px;
    color:#fff;
    font-size:14px;
    border-radius:5px;
  }
  textarea{
    min-width: 500px;
    height: 200px;
    border: 1px solid #f67000;
        margin-bottom: 20px;
  }
  .title{
    font-family: '宋体';
    margin-bottom: 20px;
  }
  .add_discuss{
    display: flex;
    margin-bottom: 20px;
  }
</style>